body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  padding: 30px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.btn {
  width: 100px;
  height: 40px;
  margin: 10px;
}

.button-group {
  display: flex;
  justify-content: center;
}

.item_table {
  display: flex;
  border-bottom: 1px solid rgba(189, 189, 189, 0.50);
}

.item_table_title {
  background: #bdbdbd;
}

.item_table div {
  height: 30px;
  padding: 0 30px;
  margin: 2px;
  display: flex;
  align-items: center;
}

.table_id {
  width: 30px !important;
}

.table_firstName, .table_lastName {
  width: 120px;
}

.table_email, .table_phone {
  width: 220px;
}

.loading, .choose {
  margin-top: 20px;
  width: 100%;
  display: flex;
  justify-content: center;
  font-size: 30px;
}

.pagination {
  margin: 20px;
}

.fullInfo textarea {
  width: 400px;
  height: 150px;
}

.input-group {
  margin: 20px;
}

.form-control {
  height: 40px !important;
}

.btn-secondary {
  margin: 0;
}
